import React from 'react';
import "./Poles.css";
import BDS_logo from "../../Assets/Logos/SPORT/BDS_logo.png";
import CGlisse_logo from "../../Assets/Logos/SPORT/cglisse_logo.png";
import CPlongee_logo from "../../Assets/Logos/SPORT/cplongee_logo.png";
import MDV_logo from "../../Assets/Logos/SPORT/MDV_logo.png";
import MRC_logo from "../../Assets/Logos/SPORT/MRC_logo.png";
import MSB_logo from "../../Assets/Logos/SPORT/MSB_logo.png";
import RAID_logo from "../../Assets/Logos/SPORT/Raid_logo.png";
import RIC_logo from "../../Assets/Logos/SPORT/RIC_logo.png";
import TVP_logo from "../../Assets/Logos/SPORT/TVP_logo.png";
import Asso from "./Asso";


export default class PoleSPORT extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            ouvert: false
        }
    }

    OuvrirFunction() {
        this.setState({
            ouvert: true
        })
    }

    FermerFunction() {
        this.setState({
            ouvert: false
        })
    }

    render() {
        if (!this.state.ouvert){
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.OuvrirFunction()}>
                        <div id={"SPORT_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Sport </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={BDS_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={CGlisse_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={CPlongee_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={MDV_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={MRC_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={MSB_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={RAID_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={RIC_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={TVP_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                        </div>
                    </div>
                </article>
            )
        }
        else{
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.FermerFunction()}>
                        <div id={"SPORT_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Sport </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={BDS_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={CGlisse_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={CPlongee_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={MDV_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={MRC_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={MSB_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={RAID_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={RIC_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                            <img src={TVP_logo} className={"LogosBTN"} alt={"Logo association SPORT"}/>
                        </div>
                    </div>
                    <div className={"Poles-Assos"}>
                        <Asso name={"BDS"} logo={BDS_logo}/>
                        <Asso name={"CGLISSE"} logo={CGlisse_logo}/>
                        <Asso name={"CPLONGEE"} logo={CPlongee_logo}/>
                        <Asso name={"MDV"} logo={MDV_logo}/>
                        <Asso name={"MRC"} logo={MRC_logo}/>
                        <Asso name={"MSB"} logo={MSB_logo}/>
                        <Asso name={"RAID"} logo={RAID_logo}/>
                        <Asso name={"RIC"} logo={RIC_logo}/>
                        <Asso name={"TVP"} logo={TVP_logo}/>
                    </div>
                </article>
            )
        }
    }
}