import React from 'react';
import "./Poles.css";
import BDA_logo from "../../Assets/Logos/CULTURE/BDA_logo.png";
import Farigoule_logo from "../../Assets/Logos/CULTURE/Farigoule_logo.png";
import PXC_logo from "../../Assets/Logos/CULTURE/PXC_logo.png";
import MASS_logo from "../../Assets/Logos/CULTURE/massiliades_logo.png";
import Asso from "./Asso";

export default class PoleCULTURE extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            ouvert: false
        }
    }

    OuvrirFunction() {
        this.setState({
            ouvert: true
        })
    }

    FermerFunction() {
        this.setState({
            ouvert: false
        })
    }

    render() {
        if (!this.state.ouvert){
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.OuvrirFunction()}>
                        <div id={"CULTURE_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Culture </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={BDA_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                            <img src={Farigoule_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                            <img src={MASS_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                            <img src={PXC_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                        </div>
                    </div>
                </article>
            )
        }
        else{
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.FermerFunction()}>
                        <div id={"CULTURE_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Culture </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={BDA_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                            <img src={Farigoule_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                            <img src={MASS_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                            <img src={PXC_logo} className={"LogosBTN"} alt={"Logo association CULTURE"}/>
                        </div>
                    </div>
                    <div className={"Poles-Assos"}>
                        <Asso name={"BDA"} logo={BDA_logo}/>
                        <Asso name={"FARIGOULE"} logo={Farigoule_logo}/>
                        <Asso name={"MASSILIADES"} logo={MASS_logo}/>
                        <Asso name={"PXC"} logo={PXC_logo}/>
                    </div>
                </article>
            )
        }
    }
}