import React from 'react';
import "./Poles.css";
import KSI_logo from "../../Assets/Logos/BUSINESS/KSI_logo.png";
import CMF_logo from "../../Assets/Logos/BUSINESS/CMF_logo.png";
import Foceen_logo from "../../Assets/Logos/BUSINESS/FOCEEN_logo.png";
import Asso from "./Asso";

export default class PoleBUSINESS extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            ouvert: false
        }
    }

    OuvrirFunction() {
        this.setState({
            ouvert: true
        })
    }

    FermerFunction() {
        this.setState({
            ouvert: false
        })
    }

    render() {
        if (!this.state.ouvert){
            return(
                <div className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.OuvrirFunction()}>
                        <div id={"BUSINESS_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Business </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={CMF_logo} className={"LogosBTN"} alt={"Logo association BUSINESS"}/>
                            <img src={Foceen_logo} className={"LogosBTN"} alt={"Logo association BUSINESS"}/>
                            <img src={KSI_logo} className={"LogosBTN"} alt={"Logo association BUSINESS"}/>
                        </div>
                    </div>
                </div>
            )
        }
        else{
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.FermerFunction()}>
                        <div id={"BUSINESS_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Business </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={CMF_logo} className={"LogosBTN"} alt={"Logo association BUSINESS"}/>
                            <img src={Foceen_logo} className={"LogosBTN"} alt={"Logo association BUSINESS"}/>
                            <img src={KSI_logo} className={"LogosBTN"} alt={"Logo association BUSINESS"}/>
                        </div>
                    </div>
                    <div className={"Poles-Assos"}>
                        <Asso name={"CMF"} logo={CMF_logo}/>
                        <Asso name={"FOCEEN"} logo={Foceen_logo}/>
                        <Asso name={"KSI"} logo={KSI_logo}/>
                    </div>
                </article>
            )
        }
    }
}