import React from 'react';
import "./Poles.css";
import EP_logo from "../../Assets/Logos/CITOYENNETE/EP_logo.png";
import CU_logo from "../../Assets/Logos/CITOYENNETE/CU_logo.png";
import ISF_logo from "../../Assets/Logos/CITOYENNETE/ISF_logo.png";
import POTAGER_logo from "../../Assets/Logos/CITOYENNETE/POTAGER_logo.png";
import AUM_logo from "../../Assets/Logos/CITOYENNETE/Aumonerie_logo.png";
import Asso from "./Asso";

export default class PoleCITOYENNETE extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            ouvert: false
        }
    }

    OuvrirFunction() {
        this.setState({
            ouvert: true
        })
    }

    FermerFunction() {
        this.setState({
            ouvert: false
        })
    }

    render() {
        if (!this.state.ouvert){
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.OuvrirFunction()}>
                        <div id={"CITOYENNETE_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Citoyenneté </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={AUM_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={CU_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={EP_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={ISF_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={POTAGER_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                        </div>
                    </div>
                </article>
            )
        }
        else{
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.FermerFunction()}>
                        <div id={"CITOYENNETE_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Citoyenneté </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={AUM_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={CU_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={EP_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={ISF_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                            <img src={POTAGER_logo} className={"LogosBTN"} alt={"Logo association CITOYENNETE"}/>
                        </div>
                    </div>
                    <div className={"Poles-Assos"}>
                        <Asso name={"AUMONERIE"} logo={AUM_logo}/>
                        <Asso name={"CHEERUP"} logo={CU_logo}/>
                        <Asso name={"EP"} logo={EP_logo}/>
                        <Asso name={"ISF"} logo={ISF_logo}/>
                        <Asso name={"POTAGER"} logo={POTAGER_logo}/>
                    </div>
                </article>
            )
        }
    }
}