import React from 'react';
import Fleche from "../Assets/LosangeCoupeDroite.png";

export default class VieAssociativeIntro extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <article id="VieAssociative-Container">
                <div className={"VieAssociative-texte"}>
                    <div className={"VieAssociative-titre"}>
                        <img alt="DecorationFLeche" id={"VieAssociative-fleche"} src={Fleche}/>
                        <h3 className={"VieAssociative-h3"}> PÔLES <br/> ASSOCIATIFS </h3>
                    </div>
                    <p className={"VieAssociative-p"}> Le fonctionnement de l’Union des Associations repose sur 6 pôles thématiques. Chacun est en dialogue permament avec son administrateur référent qui transmet toutes les informations nécessaires au bon déroulement de la vie associative et qui sert de contact privilégié pour les associations. </p>
                </div>
            </article>
        )
    }
}