import React from 'react';
import "./Poles.css";
import BDE_logo from "../../Assets/Logos/VDE/BDE_logo.png";
import Barbu_logo from "../../Assets/Logos/VDE/Barbu_logo.png";
import Foys_logo from "../../Assets/Logos/VDE/Foys_logo.png";
import TP_logo from "../../Assets/Logos/VDE/TP_logo.png";
import Chaudron_logo from "../../Assets/Logos/VDE/Chaudron_logo.png";
import Phy_logo from "../../Assets/Logos/VDE/PhyTv_logo.png";
import LPC_logo from "../../Assets/Logos/VDE/LPC_logo.png";
import ICM_logo from "../../Assets/Logos/VDE/ICM_logo.png";
import Asso from "./Asso";

export default class PoleVDE extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            ouvert: false
        }
    }

    OuvrirFunction() {
        this.setState({
            ouvert: true
        })
    }

    FermerFunction() {
        this.setState({
            ouvert: false
        })
    }

    render() {
        if (!this.state.ouvert){
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.OuvrirFunction()}>
                        <div id={"VDE_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Vie de l'Élève </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={Barbu_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={BDE_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={Chaudron_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={Foys_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={ICM_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={TP_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={LPC_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={Phy_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                        </div>
                    </div>
                </article>
            )
        }
        else{
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.FermerFunction()}>
                        <div id={"VDE_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Vie de l'Élève </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={Barbu_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={BDE_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={Chaudron_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={Foys_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={ICM_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={TP_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={LPC_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                            <img src={Phy_logo} className={"LogosBTN"} alt={"Logo association VDE"}/>
                        </div>
                    </div>
                    <div className={"Poles-Assos"}>
                        <Asso name={"BARBU"} logo={Barbu_logo}/>
                        <Asso name={"BDE"} logo={BDE_logo}/>
                        <Asso name={"CHAUDRON"} logo={Chaudron_logo}/>
                        <Asso name={"FOYS"} logo={Foys_logo}/>
                        <Asso name={"ICM"} logo={ICM_logo}/>
                        <Asso name={"TP"} logo={TP_logo}/>
                        <Asso name={"LPC"} logo={LPC_logo}/>
                        <Asso name={"PHY"} logo={Phy_logo}/>
                    </div>
                </article>
            )
        }
    }
}