import React from 'react';
import "./Poles.css";
import Chris_logo from "../../Assets/Logos/INNOVATION/Chris_logo.png";
import Egab_logo from "../../Assets/Logos/INNOVATION/EGab_logo.png";
import Fab_logo from "../../Assets/Logos/INNOVATION/Fablab_logo.png";
import GInfo_logo from "../../Assets/Logos/INNOVATION/GInfo_logo.png";
import Krypto_logo from "../../Assets/Logos/INNOVATION/Kryptosphere_logo.png";
import R2CM_logo from "../../Assets/Logos/INNOVATION/R2CM_logo.png";
import Asso from "./Asso";

export default class PoleINNO extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            ouvert: false
        }
    }

    OuvrirFunction() {
        this.setState({
            ouvert: true
        })
    }

    FermerFunction() {
        this.setState({
            ouvert: false
        })
    }


    render() {
        if (!this.state.ouvert){
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.OuvrirFunction()}>
                        <div id={"INNO_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Innovation </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={Chris_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={Egab_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={Fab_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={GInfo_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={Krypto_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={R2CM_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                        </div>
                    </div>
                </article>
            )
        }
        else{
            return(
                <article className={"Poles-container"} id="PoleVDE-Container">
                    <div className={"Poles-BTN"} onClick={() => this.FermerFunction()}>
                        <div id={"INNO_color"}/>
                        <h3 className={"Poles-h3"}> Pôle Innovation </h3>
                        <div className={"Poles-LogosAssosMin-container"}>
                            <img src={Chris_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={Egab_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={Fab_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={GInfo_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={Krypto_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                            <img src={R2CM_logo} className={"LogosBTN"} alt={"Logo association INNOVATION"}/>
                        </div>
                    </div>
                    <div className={"Poles-Assos"}>
                        <Asso name={"CHRISTOPHER"} logo={Chris_logo}/>
                        <Asso name={"EGAB"} logo={Egab_logo}/>
                        <Asso name={"FABLAB"} logo={Fab_logo}/>
                        <Asso name={"GINFO"} logo={GInfo_logo}/>
                        <Asso name={"KRYPTOSPHERE"} logo={Krypto_logo}/>
                        <Asso name={"R2CM"} logo={R2CM_logo}/>
                    </div>
                </article>
            )
        }
    }
}