import React from 'react';
import textesAssos from "./TextesAssos";
import FB_icon from "./AllerPlusLoin_icons/Facebook.png";
import Insta_icon from "./AllerPlusLoin_icons/Instagram.png";
import Linkedin_icon from "./AllerPlusLoin_icons/Linkedin.png";
import Mail_icon from "./AllerPlusLoin_icons/MAIL.png";
import WEBSITE_icon from "./AllerPlusLoin_icons/WEB.png";
import Twitter_icon from "./AllerPlusLoin_icons/Twitter.png";
import "./Asso.css";

export default class Asso extends React.Component {
    constructor(props) {
        super(props);
        this.AllerPlusLoinContent = [];
        for (let MEAN in textesAssos[this.props.name]["allerPlusLoin"]){
            if (MEAN === "FB"){this.AllerPlusLoinContent.push(<a href={textesAssos[this.props.name]["allerPlusLoin"][MEAN]}> <img src={FB_icon} alt="Icon FB" className={"AllerPlusLoin-icons"}/> </a>)}
            else if (MEAN === "INSTA"){this.AllerPlusLoinContent.push(<a href={textesAssos[this.props.name]["allerPlusLoin"][MEAN]}> <img src={Insta_icon} alt="Icon Insta" className={"AllerPlusLoin-icons"}/> </a>)}
            else if (MEAN === "TWITTER"){this.AllerPlusLoinContent.push(<a href={textesAssos[this.props.name]["allerPlusLoin"][MEAN]}> <img src={Twitter_icon} alt="Icon Insta" className={"AllerPlusLoin-icons"}/> </a>)}
            else if (MEAN === "LINKEDIN"){this.AllerPlusLoinContent.push(<a href={textesAssos[this.props.name]["allerPlusLoin"][MEAN]}> <img src={Linkedin_icon} alt="Icon Insta" className={"AllerPlusLoin-icons"}/> </a>)}
            else if (MEAN === "MAIL"){this.AllerPlusLoinContent.push(<a href={textesAssos[this.props.name]["allerPlusLoin"][MEAN]}> <img src={Mail_icon} alt="Icon Insta" className={"AllerPlusLoin-icons"}/> </a>)}
            else if (MEAN === "WEB"){this.AllerPlusLoinContent.push(<a href={textesAssos[this.props.name]["allerPlusLoin"][MEAN]}> <img src={WEBSITE_icon} alt="Icon Insta" className={"AllerPlusLoin-icons"}/> </a>)}
        }
    }

    render() {
        return (
            <div className={"Asso-container"}>
                <img src={this.props.logo} alt={"Logo de l'asso "+this.props.name} className={"VieAssociative-Assos-Logos"}/>
                <div className={"AssoText-container"}>
                    <h4 className={"Asso-h4"}>{textesAssos[this.props.name]["name"]}</h4>
                    {textesAssos[this.props.name]["texte"]}
                    <div className={"AllerPlusLoin-container"}>
                        {this.AllerPlusLoinContent}
                    </div>
                </div>
            </div>
        )
    }
}