import React from 'react';
import ImageDeGarde from "../Components/ImageDeGarde";
import VieAssociativeIntro from "../Components/VieAssociativeIntro";
import "./VieAssociative.css";
import PoleVDE from "../Components/Poles/PoleVDE";
import PoleBUSINESS from "../Components/Poles/PoleBUSINESS";
import PoleCITOYENNETE from "../Components/Poles/PoleCITOYENNETE";
import PoleCULTURE from "../Components/Poles/PoleCULTURE";
import PoleINNO from "../Components/Poles/PoleINNO";
import PoleSPORT from "../Components/Poles/PoleSPORT";
import ImageVide from "../Assets/ImgVide.png";

export default class VieAssociative extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <section className={"Pages"} id="VieAssociative-Container">
                <div className={"LeftColumn"}/>
                <div className={"MiddleColumn"} id={"Content_AccueilContainer"}>
                    <ImageDeGarde Page={"VieAssociative"}/>
                    <img className={"emptyImage"} src={ImageVide}/>
                    <VieAssociativeIntro/>
                    <PoleVDE/>
                    <PoleBUSINESS/>
                    <PoleCITOYENNETE/>
                    <PoleCULTURE/>
                    <PoleINNO/>
                    <PoleSPORT/>
                </div>
                <div className={"RightColumn"}/>
            </section>
        )
    }
}